<template>
    <div>

        <!---->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item> </el-breadcrumb-item>
        </el-breadcrumb>

        <el-card>

            <el-divider></el-divider>

            <el-tag type="primary">{{register.registerId}} </el-tag>
            <el-tag type="success">{{register.patientName}} </el-tag>
            <el-tag type="warning">{{register.patientGender}} </el-tag>

            <el-divider></el-divider>

            <el-input v-model="speak" placeholder="" type="textarea" row="2" style="width: 800px"></el-input>
            <el-button type="primary" class="button-update" @click="modify"></el-button>

            <el-divider></el-divider>

            <el-button type="primary" @click="openDialog"></el-button>

            <el-button type="success" @click="addCheck" class="add-button"></el-button>


            <!---->
            <el-table
                    :data="checkList"
                    stripe
                    border
            >
                <el-table-column prop="checkName" label=""/>
                <el-table-column prop="checkAmount" label=""/>


                <el-table-column label="" align="center">
                    <template slot-scope="scope">

                        <el-button
                                type="danger" size="mini"
                                @click="deleteCheck(scope.row.checkId,scope.row.checkName )"></el-button>
                    </template>
                </el-table-column>

            </el-table>

            <!---->

            <el-dialog :visible.sync="openDialogFlag" width="60%" center>

                <!---->
                <el-input v-model="queryCondition.checkName" placeholder="" clearable/>
                <el-input v-model="queryCondition.checkCode" placeholder="" clearable/>

                <el-button type="primary" @click="getCheckList"></el-button>

                <el-button type="primary" @click="clear"></el-button>


                <!---->
                <el-table
                        :data="checkListDialog"
                        style="width: 80%"
                        stripe
                        border
                        @selection-change="handleSelectionChange"
                >
                    <el-table-column type="selection"/>
                    <el-table-column prop="checkName" label=""/>
                    <el-table-column prop="checkCode" label=""/>
                    <el-table-column prop="checkAmount" label=""/>


                </el-table>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="openDialogFlag = false"> </el-button>
                    <el-button type="primary" @click="addCheckDialog"> </el-button>
                </div>
            </el-dialog>

        </el-card>

    </div>
</template>

<script>
    export default {

        data(){
            return{

                register: this.$route.query.register,

                speak:'',

                checkList: [],

                openDialogFlag: false,

                queryCondition: {
                    checkName:'',
                    checkCode:'',
                },

                checkListDialog:[],

                checksDialog:[],


            }
        },

        methods:{

            modify(){

                let fd = new FormData();
                fd.append("registerId",this.register.registerId)
                fd.append("speak",this.speak)

                this.$http.put("/api/register/modify" , fd).then(

                    res=>{
                        if (res.data.status == 1){
                            this.$message.success(res.data.message)
                        }else {
                            this.$message.error(res.data.message)
                        }
                    }

                )

            },

            openDialog(){

                this.queryCondition.checkName = ''
                this.queryCondition.checkCode = ''
                this.openDialogFlag = true

                this.getCheckList()

            },



            getCheckList(){

                this.$http.get('/api/check/listAll' , {params: this.queryCondition}).then(

                    res=>{
                        this.checkListDialog  = res.data.data.checkList
                    }

                )

            },

            clear(){

                this.queryCondition.checkName = ''
                this.queryCondition.checkCode = ''

                this.checkListDialog = []

            },

            async addCheck(){

                if (this.checkList.length == 0){
                    this.$message.error('')
                    return
                }

                let checkIds = ''

                this.checkList.forEach(check =>{
                    checkIds += check.checkId + ','
                })

                checkIds = checkIds.substring(0,checkIds.length-1)



                const result = await this.$confirm('', '').catch(()=>{})

                if (result == 'confirm'){

                    let fd = new FormData();
                    fd.append("registerId",this.register.registerId)
                    fd.append("checkIds",checkIds)

                    this.$http.post('/api/request/add',fd).then(
                        res=>{
                            if (res.data.status == 1){
                                this.$message.success(res.data.message)
                                this.checkList = []
                            }else {
                                this.$message.error(res.data.message)
                            }


                        }
                    )

                }


            },

            //
            async addCheckDialog(){

                const result = await this.$confirm('', '')

                if (result == 'confirm'){
                    this.checksDialog.forEach(check =>{
                        let existFlag = false
                        this.checkList.forEach(checkItem =>{
                            if (checkItem.checkId == check.checkId){
                                existFlag = true
                            }
                        })
                        
                        if (!existFlag){
                            this.checkList.push(check)
                        }
                        
                    })
                }
                this.openDialogFlag = false

            },

            handleSelectionChange(val){
                this.checksDialog = val
            },

            async deleteCheck(checkId,checkName){
                const result = await this.$confirm('' + checkName + '','')

                if (result == 'confirm'){
                    const  index = this.checkList.findIndex(check=>{
                        return check.checkId == checkId
                    })

                    this.checkList.splice(index,1)
                }

            }

        },

        mounted(){
        },

    }
</script>

<style scoped>
    .el-tag{
        margin: 10px;
    }
    .button-update{
        margin-left: 20px;
    }

    .add-button{
        position: absolute;
        right: 223px;
    }

</style>