<template>
  <div>

    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-divider></el-divider>
      <el-tag>{{ register.registerId }}</el-tag>
      <el-tag type="success">{{ register.patientName }}</el-tag>
      <el-tag type="warning">{{ register.patientGender }}</el-tag>

      <el-divider></el-divider>

      
      <el-input v-model="result" placeholder="" style="width:450px"/>

      
      <el-select v-model="diseaseId" style="width:150px;margin-right: 30px;margin-left: 10px">

        <el-option v-for="disease in diseaseList" :label="disease.diseaseName" :value="disease.diseaseId"/>

      </el-select>

      <el-button type="primary" @click="insure"></el-button>

      <el-divider></el-divider>

      <el-button type="primary" @click="openDialog"></el-button>

      <el-button type="seccuss" @click="addPrescription" class="add-button"></el-button>

      <el-table :data="drugList" stripe border>

        <el-table-column type="index" label="" width="100px" align="center"/>

        <el-table-column prop="drugName" label=""/>
        <el-table-column prop="drugPrice" label="()"/>
        <el-table-column prop="quantity" label=""/>
        <el-table-column prop="times" label=""/>
        <el-table-column prop="count" label=""/>

        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button type="danger" size="mini" @click="deleteDrug(scope.row.drugId,scope.row.drugName)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!---->
    <el-dialog title="" :visible.sync="openDialogFlag" width="50%" center>

      <el-input v-model="queryCondition.drugName" placeholder="" style="width: 180px;" clearable/>
      <el-input v-model="queryCondition.drugCode" placeholder="" style="width: 180px;" clearable/>

      <el-button type="primary" @click="getDrugList"></el-button>
      <el-button type="primary" @click="clear"></el-button>

      <el-table :data="drugListDialog" stripe border @selection-change="handleSelectionChange">

        <el-table-column type="selection"/>

        <el-table-column label="expand">
          <template slot-scope="scope">
            <el-input v-model="scope.row.quantity" placeholder="" style="width: 150px;" class="drug-usage"/>
            <el-input v-model="scope.row.times" placeholder="" style="width: 150px;" class="drug-usage"/>
            <el-input v-model="scope.row.count" placeholder="" style="width: 150px;" class="drug-usage"/>
          </template>
        </el-table-column>

        <el-table-column prop="drugName" label=""/>
        <el-table-column prop="drugCode" label=""/>
        <el-table-column prop="drugPrice" label="()"/>

      </el-table>
      <!---->
      <div slot="footer">

        <el-button @click="openDialogFlag=false"></el-button>

        <el-button type="primary" @click="addDrugDialog"></el-button>

      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {

  data() {

    return {

      register: this.$route.query.register,

      result: '',

      diseaseId: '',

      diseaseList: [],

      drugList: [],

      openDialogFlag: false,

      queryCondition: {
        drugName: '',
        drugCode: '',
      },

      drugListDialog: [],

      drugsDialog: [],


    }
  },

  methods: {
    insure() {
      let fd = new FormData();

      fd.append('registerId', this.register.registerId);
      fd.append('result', this.result);
      fd.append('diseaseId', this.diseaseId);

      this.$http.put('/api/register/insure', fd).then(
          res => {

            if (res.data.status == 1) {

              this.$message.success('' + this.register.registerId + '');

            } else {

              this.$message.error('' + this.register.registerId + '');

            }
          }
      )
    },

    openDialog() {
      this.queryCondition.drugCode = ''
      this.queryCondition.drugName = ''

      this.drugListDialog = []
      this.openDialogFlag = true
    },

    getDrugList() {
      this.$http.get('/api/drug/listAll', {params: this.queryCondition}).then(
          res => {
            this.drugListDialog = res.data.data.drugList

          }
      )
    },

    clear() {
      this.queryCondition.drugCode = ''
      this.queryCondition.drugName = ''

      this.drugListDialog = []
    },


    //
    handleSelectionChange(val) {

      this.drugsDialog = val;
    },


    async addDrugDialog() {

      const result = await this.$confirm('', '')
      if (result == 'confirm') {

        this.drugsDialog.forEach(drug => {
          let existFlag = false;
          this.drugList.forEach(drugItem => {
            if (drugItem.drugId == drug.drugId) {
              existFlag = true
            }
          })
          if (!existFlag) {
            drug.registerId = this.register.registerId
            this.drugList.push(drug)
          }

        })

        this.openDialogFlag = false

      }
    },


    async deleteDrug(drugId, drugName) {

      const result = await this.$confirm('' + drugName + '', '')

      if (result == 'confirm') {

        const index = this.drugList.findIndex(drug => {
          return drug.drugId == drugId
        })
        this.drugList.splice(index, 1)

      }
    },

    //
    async addPrescription() {

      if (this.drugList.length == 0) {

        this.$message.error("")
        return
      }

      const result = await this.$confirm('', '')

      if (result == 'confirm') {

        this.$http.post('/api/prescription/add', this.drugList).then(
            res => {

              if (res.data.status == 1) {

                this.$message.success(res.data.message)

                this.$router.push('/insure')

              } else {

                this.$message.error(res.data.message)

              }
            }
        )
      }
    }
  },


  mounted() {
    this.$http.get('/api/disease/listAll').then(
        res => {
          this.diseaseList = res.data.data.diseaseList
        }
    )
  }
}
</script>

<style scoped>
.el-tag {
  margin: 10px;
}

.add-button {
  position: absolute;
  right: 84px;
}

.drug-usage {
  margin-left: 22px;
}
</style>
