<template>
	<div>
		
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		
		<!--  -->
		<el-card>
                <el-form :model="formData" label-width="100px" :rules="formRules" ref="formRef">
                    <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="patientName">
                            <el-input v-model="formData.patientName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="patientName">
                            <el-radio-group v-model="formData.patientGender">
                                <el-radio label=""></el-radio>
                                <el-radio label=""></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop="regiserDate">
                                <el-input v-model="formData.registerDate" type="date"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-radio-group v-model="formData.registerNoon">
                                    <el-radio label="0"></el-radio>
                                    <el-radio label="1"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-radio-group v-model="formData.settleMode">
                                    <el-radio label="1"></el-radio>
                                    <el-radio label="2"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="" prop="feeMode">
                                <el-select v-model="formData.feeMode" placeholder="">
                                    <el-option v-for="feeType in feeTypes" :value="feeType.typeId" :label="feeType.typeName"/>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-select v-model="formData.deptId" placeholder="" prop="deptId">
                                    <el-option v-for="dept in deptList" :value="dept.deptId" :label="dept.deptName"/>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-select v-model="formData.levelId" placeholder="" prop="levelId" @change="changeLevel">
                                    <el-option v-for="level in levelList" :value="level.levelId" :label="level.levelName"/>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="">
                                <el-select v-model="formData.doctorId" placeholder="" prop="doctorId">
                                    <el-option v-for="doctor in doctorList" :value="doctor.doctorId" :label="doctor.doctorName"/>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="4">
                            <el-button type="primary" @click="selectDoctor"></el-button>
                        </el-col>
                        <el-col :span="4">
                           <el-form-item label="">
                               <el-switch v-model="formData.needBook" @change="changeBook"/>
                           </el-form-item>
                        </el-col>
                        <el-col :span="4">
                            <el-tag>{{formData.registerAmount}}</el-tag>
                        </el-col>
                        <el-col :span="4">
                            <el-button type="primary" @click="addRegister"></el-button>
                        </el-col>
                    </el-row>
                </el-form>
        </el-card>
		

	</div>
</template>

<script>
	export default {

        data() {

            return {

                //


                //
                queryCondition: {

                    registerDate: '',
                    registerNoon: '',
                    deptId: '',
                    levelId: '',

                },


                //
                formData: {
                    patientName: '',
                    patientGender: '',
                    registerDate: '',
                    registerNoon: '',
                    settleMode: '',
                    feeMode: '',
                    deptId: '',
                    levelId: '',
                    doctorId: '',
                    needBook: '',
                    registerAmount: 0,
                },

                //
                formRules: {
                    patientName: [
                        {required: true, message: '', trigger: 'blur'},
                        {max: 10, message: '10', trigger: 'blur'}
                    ],
                    registerDate: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    feeMode: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    deptId: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    levelId: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    doctorId: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                },
                feeTypes: [
                    {typeId: 1, typeName: ''},
                    {typeId: 2, typeName: ''},
                    {typeId: 3, typeName: ''},
                    {typeId: 4, typeName: ''},
                ],
                deptList: [],
                levelList: [],
                doctorList: [],


            }
        },

        methods: {
            //
            changeLevel(levelId) {
                this.levelList.forEach(level => {
                    if (level.levelId == levelId) {
                        this.formData.registerAmount = level.levelAmount
                    }
                })
            },

            changeBook(needBook) {
                if (needBook) {
                    this.formData.registerAmount++;
                } else {
                    this.formData.registerAmount--
                }
            },
            selectDoctor() {
                this.queryCondition.registerDate = this.formData.registerDate
                this.queryCondition.registerNoon = this.formData.registerNoon
                this.queryCondition.deptId = this.formData.deptId
                this.queryCondition.levelId = this.formData.levelId
                console.log(this.queryCondition)
                this.$http.get('/api/doctor/listReg', {params: this.queryCondition}).then(
                    res => {
                        this.doctorList = res.data.data.doctorList
                    }
                )
            },
            addRegister() {
                this.$http.post('/api/register/add', this.formData).then(
                    res => {
                        if (res.data.status == 1) {
                            this.$message.success(res.data.message)
                            this.formData = {}
                        } else {
                            this.$message.error(res.data.message)
                        }
                    }
                )
            },

        },
        mounted() {
            this.$http.get('/api/dept/listAll/1').then(
                res => {
                    this.deptList=res.data.data.deptList
                }
            )
            this.$http.get('/api/level/listAll').then(
                res => {
                    this.levelList=res.data.data.levelList
                }
            )
        }
    }
</script>

<style scoped>
.add-button{
	position: absolute;
	right: 40px;
}
.delete-button{
	position: absolute;
	right: 200px;
}
</style>
