<template>
	<div>
		
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
        <el-input v-model="queryCondition.registerId" placeholder="" clearable style="width: 240px"/>
        <el-select v-model="queryCondition.status" placeholder="" style="width: 160px;margin-right: 20px">
            <el-option label="" value=""/>
            <el-option label="" value="1"/>
            <el-option label="" value="2"/>
            <el-option label="" value="3"/>
        </el-select>
        <el-button type="primary" @click="getCheckList"></el-button>
        <el-table :data="checkRequestVOList" stripe border >

            <el-table-column type="index" label="" align="center"/>

            <el-table-column prop="requestId" label="" />
            <el-table-column prop="patientName" label="" />
            <el-table-column prop="checkName" label="" />
            <el-table-column prop="status" label="" />
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini"
                               @click="execute(scope.row)" v-if="scope.row.status==''"> <!---->
                        
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

	</div>
</template>

<script>

    export default {

        data() {

            return {
                //

                checkRequestVOList: [],
                //
                queryCondition: {
                    status: '2',
                    registerId: '',

                },
            }
        },

      methods: {

          //
        getCheckList() {

              this.$http.get('/api/request/list', {params: this.queryCondition}).then(
                  res => {

                    console.log(res.data.data.checkRequestVOList)
                      this.checkRequestVOList = res.data.data.checkRequestVOList
                      console.log(this.checkRequestVOList)
                      this.checkRequestVOList.forEach(check => {
                          if (check.status == 1) {
                              check.status = ""
                          } else if (check.status == 2) {
                              check.status = ""
                          } else {
                              check.status = ""
                          }
                      })

                  }
              )
          },
          execute(checkRequest) {

              // console.log(checkRequest)
              this.$router.push({path: '/inputResult', query: {checkRequest: checkRequest}})
          },
      },
      mounted() {
        this.getCheckList();
      }
    }
</script>

<style scoped>
.add-button{
	position: absolute;
	right: 40px;
}
.delete-button{
	position: absolute;
	right: 160px;
}
</style>
