<template>
	<div>
		
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		
		<!--  -->
		<el-card>
			
			<!---->
			<el-input v-model="registerId" placeholder="" style="width: 200px;"/>
			<el-button type="primary" @click="getCheckList" ></el-button>

			
			<!--  -->
			<el-table :data="checkVOList" stripe border>

        <el-table-column type="index" align="center" width="100"/>
        <el-table-column prop="checkName" label=""/>
        <el-table-column prop="checkAmount" label=""/>
        <el-table-column prop="checkTime" label=""/>

      </el-table>
      <el-tag>{{totalAmount}}</el-tag>
      <el-button type="primary" @click="charge" :disabled="checkVOList.length==0"></el-button>

      <el-table :data="drugVOList" stripe border>

        <el-table-column type="index" label="" align="center" width="100"/>
        <el-table-column prop="drugName" label=""/>
        <el-table-column prop="drugPrice" label=""/>
        <el-table-column prop="quantity" label=""/>
        <el-table-column prop="totalPrice" label="()"/>
        <el-table-column prop="createTime" label=""/>

      </el-table>

      <el-tag>{{totalAmountDrug}}</el-tag>

      <el-button type="primary" @click="chargeDrug" :disabled="drugVOList.length==0"></el-button>
		</el-card>
	</div>
</template>

<script>
	export default {

      data() {
          return {
            checkVOList: [],
            registerId: '',
            drugVOList:[],
          }
      },

        methods: {
            //
            getCheckList() {
                this.$http.get('/api/request/list/'+ this.registerId).then(

                    res => {
                      this.checkVOList = res.data.data.checkVOList
                      console.log(this.checkVOList)
                    }
                )

              this.$http.get('/api/prescription/list/' + this.registerId + '/1').then(
                  res => {
                    this.drugVOList = res.data.data.drugVOList
                  }
              )
            },

            async charge() {
                const result = await this.$confirm('' + this.totalAmount + '', '');
                if (result == 'confirm') {
                    this.$http.put('/api/request/fee/' + this.registerId).then(
                        res => {
                            if (res.data.status == 1) {
                                this.$message.success(res.data.message)
                                this.registerId = ''
                                this.checkVOList = []
                            } else {
                                this.$message.error(res.data.message)
                            }
                        }
                    )
                }
            },

            async chargeDrug(){
              const  result = await this.$confirm('' + this.totalAmountDrug + '','' )

              if(result == 'confirm'){

                this.$http.put('/api/prescription/fee/' + this.registerId).then(
                    res => {
                      if(res.data.status == 1){
                        this.$message.success(res.data.message)

                        this.registerId = ''

                        this.drugVOList = []
                      }else {
                        this.$message.error(res.data.message)
                      }
                    }
                )
              }
            }
        },
        computed: {
            totalAmount() {
                let checkAmount = 0
                this.checkVOList.forEach(checkVO => {
                        checkAmount += checkVO.checkAmount
                    }
                )
                return checkAmount
            },

          totalAmountDrug(){

              let drugAmount = 0

            this.drugVOList.forEach(drugVO => {
              console.log(drugVO)
              drugAmount += drugVO.totalPrice
            })

            return drugAmount
          }
        },
        mounted() {

        }
    }
</script>

<style scoped>
    .el-tag{
        margin: 0px 60px;
    }
</style>
