<template>
  <!--  -->
  <el-container class="root_container">

    <!--  -->
    <el-header>

      <img src="@/assets/image/logo.png" />
      <span>HIS</span>
      <span class="user-info">
        <i class="el-icon-user"></i>{{ currentUser.doctorName }}
      </span>
      <span class="user-info">
        <i class="el-icon-bank-card"></i>{{ doctorType }}
      </span>
      <span class="user-info" v-if="currentUser.doctorType == 1 || currentUser.doctorType == 2">
        <i class="el-icon-copy-document"></i>{{ deptName }}
      </span>
      <el-button type="primary" @click="logout"></el-button>

    </el-header>

    <el-container>

      <!--   -->
      <el-aside width="200px">

        <el-menu background-color="#48c0a3" text-color="white" active-text-color="#ffd04b" unique-opened router>

          <!--  -->
          <el-submenu index="1" v-if="currentUser.doctorType == 1">

            <template slot="title">
              <i class="el-icon-location"></i>
              <span></span>
            </template>
            <el-menu-item index="receive">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>
            <el-menu-item index="insure">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

          </el-submenu>
          <el-submenu index="2" v-if="currentUser.doctorType == 2">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span></span>
            </template>
            <el-menu-item index="execute">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>
            <el-menu-item index="inputResult">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>
          </el-submenu>
          <el-submenu index="3" v-if="currentUser.doctorType == 3">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span></span>
            </template>
            <el-menu-item index="register">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>
            <el-menu-item index="fee">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>
            <el-menu-item index="takeDrug">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>
          </el-submenu>
          <el-submenu index="4" v-if="currentUser.doctorType == 4">

            <template slot="title">
              <i class="el-icon-location"></i>
              <span></span>
            </template>
            <el-menu-item index="level" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

            <el-menu-item index="dept" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

            <el-menu-item index="disease" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

            <el-menu-item index="drug" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

            <el-menu-item index="check" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

            <el-menu-item index="schedule" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

            <el-menu-item index="doctor" style="height:40px">
              <i class="el-icon-menu"></i>
              <span></span>
            </el-menu-item>

          </el-submenu>

        </el-menu>

      </el-aside>

      <!--  -->
      <el-main>
        <!--  -->
        <router-view />
      </el-main>


    </el-container>
  </el-container>
</template>

<script>

export default {
  data() {
    return {
      currentUser: {},

      doctorType: this.$route.query.doctorType,

      deptName: this.$route.query.deptName,
    }
  },
  methods: {
    logout() {
      const result = this.$confirm(
        '', '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).then(() => {
        this.$http.post('/api/doctor/logout').then(
          res => {
            if (res.data.status == 1) {
              sessionStorage.removeItem("currentUser")

              localStorage.removeItem("loginInfo")

              this.$message.success(res.data.message)

              this.$router.push("/login")
            }
          }
        )
      }).catch(() => { })

    }
  },
  mounted() {

    if (sessionStorage.getItem("currentUser") != null) {
      this.currentUser = JSON.parse(sessionStorage.getItem("currentUser"))
    }
  }
}

</script>

<style scoped>
.el-header {
  background-color: #48c0a3;
  color: white;
  font-size: 24px;

  /*  */
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.el-aside {
  background-color: #48c0a3;
}

.root_container {
  height: 100%;
}

.el-menu {
  border-right: none;
}

.user-info {
  color: ffffff;
  font-size: 16px;
}
</style>
