<template>
	<div>
		
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
		  <el-breadcrumb-item :to="{path: '/'}"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		
		<!--  -->
		<el-card>
			
			<!---->
			<el-input v-model="queryCondition.registerId" placeholder="" clearable style="width: 200px"></el-input>
      <el-input v-model="queryCondition.patientName" placeholder="" clearable style="width: 200px"></el-input>
			<el-select v-model="queryCondition.status" placeholder="" style="width: 160px;margin-right: 20px">
				<el-option label="" value=""/>
				<el-option label="" value="1"/>
				<el-option label="" value="2"/>
				<el-option label="" value="3"/>
			</el-select>
			
			<!---->
			<el-button type="primary" @click="getRegisterList"></el-button>
			
			<!---->
			<el-button type="primary" class="insure-button" @click="insure" :disabled="!enableInsure"></el-button>
			<!--  -->
			<el-table :data="registerList" stripe border>
                <el-table-column type="index"  label="" width="100px" align="center" />
                <el-table-column prop="registerId" label=""/>
                <el-table-column prop="patientName" label=""/>
                <el-table-column prop="patientGender" label=""/>
				<el-table-column prop="status" label=""/>

                <el-table-column label="" align="center">
					
					<template slot-scope="scope">
						<!---->
						<el-button type="success" size="mini" @click="view(scope.row)" v-if="scope.row.status==''"></el-button>
					</template>
					
					</el-table-column>
			    </el-table>
			<el-divider>{{register.registerId}}</el-divider>
			<!--  -->
			<el-table :data="checkResultVOList" stripe border>
			    <el-table-column type="index"  label="" width="100px" align="center" />
				<el-table-column type="expand">
					<template slot-scope="scope">
				//		<img v-for="photo in scope.row.checkPhotos" :src="'/api/image/check/'+photo" class="check-photo" @click="zoomVisible">
					</template>
				</el-table-column>
			    <el-table-column prop="checkName" label=""width="150"/>
			    <el-table-column prop="status" label=""width="100"/>
			    <el-table-column prop="checkResult" label=""width="300"/>
				<el-table-column prop="checkTime" label=""width="200"/>
			    <el-table-column prop="doctorName" label=""width="100"/>
					
				</el-table>
				<!---->
				<el-dialog :title="''+checkName+''" :visible.sync="zoomVisible" width="50%">
					<img :src="imageUrl" width="100%"/>
				</el-dialog>
		</el-card>
	</div>
</template>

<script>
	export default {

        data() {
           return{
			
			//
					registerList:[],	
					//
					queryCondition:{						
						registerId:'',	//
						patientName:'',//
						status:'2', // 
					},
					
					//
					register:'',
					//
					checkResultVOList:[],
					//
					enableInsure:false,
					//
					zoomVisible:false,
					//
					imageUrl:'',
					//
					checkName:'',
			   
		   }
		   
	},	

        methods: {
          //
          getRegisterList() {
              this.$http.get('/api/register/list', {params:this.queryCondition}).then(
                  res => {
                    console.log(res.data.data)
                      this.registerList = res.data.data.registerList
                  //intstring
                      this.registerList.forEach(register=>{
                        if(register.status==1){
                          register.status="";
                        }else if(register.status==2){
                          register.status="";
                        }else{
                          register.status="";
                        }
                      })
                  }
              )
				      //
				      this.enableInsure=false
            },
          //
          view(register) {

			      this.register=register;

			      //axios
             this.$http.get('/api/request/view/'+ register.registerId).then(
                 res => {
					  //
                 this.checkResultVOList = res.data.data.checkResultVOList
                 //
                 let resultCount=0
                 //
                   this.checkResultVOList.forEach(checkResultVO=>{
                 //+1
                 if(checkResultVO.status==3){
                   resultCount++
                 }
                 //
                 checkResultVO.checkPhotos=[]
                 //intstring
                 if(checkResultVO.status==1){
                  checkResultVO.status=""
                 }else if(checkResultVO.status==2){
                  checkResultVO.status=""
                 }else{
                  checkResultVO.status=""
                  //
                  if(checkResultVO.checkPhoto!=''){
                    checkResultVO.checkPhotos=checkResultVO.checkPhoto.split("#")
                  }
                 }
               })
         			//
         			this.enableInsure= (resultCount==this.checkResultVOList.length)
                 }
             )
         }, 
           //
			zoom(imageUrl,checkName){
				//url
				this.imageUrl=imageUrl
				//
				this.checkName=checkName
				//
				this.zoomVisible=true
			},
			//
			insure(){
				//
				this.$router.push({path:'/addPrescription',query:{register:this.register}})
			},
        },
        mounted() {
         //
		       this.getRegisterList();
        }
    }
</script>

<style scoped>
    .check-photo{
       width: 100px;
	   cursor: pointer;
	   margin-right: 20px;
    }
	.insure-button{
		position: absolute;
		right: 90px;
	}
</style>
