<template>
  <div>

    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>

      <!---->
      <el-input v-model="registerId" placeholder="" style="width: 200px;"/>
      <el-button type="primary" @click="getDrugList" ></el-button>


      <!--  -->
      <el-table :data="drugVOList" stripe border>

        <el-table-column type="index" label="" align="center" width="100"/>
        <el-table-column prop="drugName" label=""/>
        <el-table-column prop="drugPrice" label=""/>
        <el-table-column prop="quantity" label=""/>
        <el-table-column prop="totalPrice" label="()"/>
        <el-table-column prop="createTime" label=""/>

      </el-table>

      <el-button type="primary" @click="takeDrug" :disabled="drugVOList.length==0"></el-button>
    </el-card>
  </div>
</template>

<script>
export default {

  data() {
    return {
      drugVOList: [],
      registerId: '',
    }
  },

  methods: {
    //
    getDrugList() {
      this.$http.get('/api/prescription/list/'+ this.registerId + '/2').then(

          res => {
            this.drugVOList = res.data.data.drugVOList

          }
      )
    },

    async takeDrug() {
      const result = await this.$confirm('' , '');
      if (result == 'confirm') {
        this.$http.put('/api/prescription/take/' + this.registerId).then(
            res => {
              if (res.data.status == 1) {
                this.$message.success(res.data.message)
                this.registerId = ''
                this.drugVOList = []
              } else {
                this.$message.error(res.data.message)
              }
            }
        )
      }
    },

  },

  mounted() {

  }
}
</script>

<style scoped>

</style>
