<template>
    <div>

        <!---->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!---->
        <el-card>

            <!---->
            <el-input v-model="queryCondition.registerId" placeholder="" style="width: 250px"/>
            <el-input v-model="queryCondition.patientName" placeholder="" style="width: 250px" />
            <el-select v-model="queryCondition.status" placeholder="" style="width: 220px;margin-right: 20px">
                <el-option label="" value=""></el-option>
                <el-option
                        v-for="status in statuses"
                        :value="status.statusId"
                        :label="status.statusName">
                </el-option>
            </el-select>

            <el-button type="primary" @click="getRegisterList"></el-button>


            <!---->
            <el-table
                    :data="registerList"
                    style="width: 80%"
                    stripe
                    border
            >
                <el-table-column type="index" align="center"/>
                <el-table-column prop="registerId" label=""/>

                <el-table-column prop="patientName" label="" />
                <el-table-column prop="patientGender" label=""/>
                <el-table-column prop="status" label=""/>

                <el-table-column label="" align="center">
                    <template slot-scope="scope" >
                        <!--register-->
                        <el-button type="primary" size="small" @click="receive(scope.row)" v-if="scope.row.status == '' "></el-button>

                    </template>
                </el-table-column>

            </el-table>

            <el-row>
                <el-col :span="5"><el-tag type="primary">{{newCount}}</el-tag></el-col>
                <el-col :span="5"><el-tag type="success">{{receiveCount}}</el-tag></el-col>
                <el-col :span="5"><el-tag type="warning">{{finishCount}}</el-tag></el-col>
                <el-col :span="5"><el-tag type="danger">{{totalCount}}</el-tag></el-col>
            </el-row>


        </el-card>

    </div>
</template>

<script>
    export default {
        data() {
            return {

                registerList: [],

                //    
                queryCondition:{
                    registerId: '',
                    patientName: '',
                    status: 1,
                },


                //
                statuses:[
                    { statusId: 1, statusName: ''},
                    { statusId: 2, statusName: ''},
                    { statusId: 3, statusName: ''},
                ],

                newCount:0,
                receiveCount:0,
                finishCount:0,
                totalCount:0,
            }
        },

        methods:{

            getRegisterList(){

                this.$http.get('/api/register/list' ,{params: this.queryCondition}).then(

                    res =>{
                        this.registerList = res.data.data.registerList
                        console.log(this.registerList)
                        this.registerList.forEach(register =>{
                            if (register.status == 1){
                                register.status = ''
                            } else if (register.status == 2) {
                                register.status = ''
                            } else {
                                register.status = ''
                            }
                        })
                    }
                )
            },

            getRegisterCount(){


                this.$http.get('/api/register/count').then(

                    res =>{
                        this.newCount = res.data.data.newCount
                        this.receiveCount = res.data.data.receiveCount
                        this.finishCount = res.data.data.finishCount
                        this.totalCount = res.data.data.totalCount


                    }
                )
            },

            receive(register){

                this.$router.push({path: '/addCheck' , query:{register: register}})

            },

        },

        mounted(){

            this.getRegisterList()

            this.getRegisterCount()

            // window.setInterval(()=>{this.getRegisterCount()},1000*5)

        }
    }
</script>

<style scoped>

    .el-tag{
        margin: 0 60px;
    }

</style>