<template>
  <div>

    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-input v-model="queryCondition.registerId" placeholder="" style="width:100px;margin-right: 10px"/>
    <el-select v-model="queryCondition.checkResult" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option v-for="checkResult in Results" :label="checkResult.resultName" :value="checkResult.resultId">
      </el-option>
    </el-select>
    <el-button type="primary" @click="getcheckList"></el-button>
    <el-table :data="checkResultList" stripe border >

      <el-table-column type="index" align="center"/>

      <el-table-column prop="checkId" label="" />
      <el-table-column prop="checkName" label="" />
      <el-table-column prop="checkCode" label="" />
      <el-table-column prop="checkAmount" label="" />
      <el-table-column prop="checkAmount" label="" />
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button type="primary" size="small"
                     @click="checkRequest(scope.row)" v-if="scope.row.status==''">
            
          </el-button>
        </template>
      </el-table-column>


      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="modify(scope.row.checkId)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="remove(scope.row.checkId,scope.row.checkName)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-card>

      <!--			&lt;!&ndash;&ndash;&gt;
            <el-input v-model="queryCondition.checkName" placeholder="" style="width:100px;margin-right: 10px"/>
            <el-input v-model="queryCondition.checkCode" placeholder="" style="width:100px;margin-right: 10px"/>
            <el-input v-model="queryCondition.minAmount" placeholder="" style="width:100px;"/>
            
            <el-input v-model="queryCondition.maxAmount" placeholder="" style="width:100px;margin-right: 10px"/>


            <el-button type="primary" @click="getcheckList" ></el-button>

            <el-button type="primary" @click="add" class="add-button"></el-button>

            <el-button type="danger" @click="deleteBatch" class="delete-button"></el-button>

            <el-table :data="checkPage.records" stripe border @selection-change="handleSelectionChange">

              <el-table-column type="selection"/>

              <el-table-column prop="checkId" label="" />
              <el-table-column prop="checkName" label="" />
              <el-table-column prop="checkCode" label="" />
              <el-table-column prop="checkAmount" label="" />

              <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="modify(scope.row.checkId)"></el-button>
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="remove(scope.row.checkId,scope.row.checkName)"></el-button>
                    </template>
                </el-table-column>
                </el-table>

              &lt;!&ndash;&ndash;&gt;
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryCondition.currentPage"
                :page-sizes="[2,3,5,10]"
                :page-size="queryCondition.pageSize"
                layout="total,sizes,prev,pager,next,jumper"
                :total="checkPage.total">
              </el-pagination>-->
    </el-card>

    <!---->
    <el-dialog :title="actionType" :visible.sync="openDialogFlag" width="30%" center>

      <el-form :model="formData" label-width="100px" :rules="formRules" ref="formRef">

        <el-form-item label="" prop="checkName">
          <el-input v-model="formData.checkName"></el-input>
        </el-form-item>

        <el-form-item label="" prop="checkCode">
          <el-input v-model="formData.checkCode"></el-input>
        </el-form-item>

        <el-form-item label="" prop="checkAmount">
          <el-input v-model.number="formData.checkAmount"></el-input>
        </el-form-item>

      </el-form>

      <!---->
      <div slot="footer">

        <el-button @click="openDialogFlag=false"></el-button>

        <el-button type="primary" @click="addOrModify"></el-button>

      </div>
    </el-dialog>
  </div>
</template>

<script>

export default {

  data() {

    return {

      //

      checkPage:{},

      //
      queryCondition:{

        checkName:'',

        checkCode:'',

        minAmount:'',

        maxAmount:'',

        currentPage:1,

        pageSize:10,

      },

      //
      openDialogFlag:false,

      //
      formData:{
        checkId:0,
        checkName:'',
        checkCode:'',
        checkAmount:0,
      },

      //
      formRules:{
        checkName:[
          {required:true,message:'',trigger:'blur'},
          {max:10,message:'10',trigger:'blur'}
        ],
        checkCode:[
          {required:true,message:'',trigger:'blur'},
          {max:10,message:'10',trigger:'blur'}
        ],
        checkAmount:[
          { required:true,message:'',trigger:'blur' },
          {type:'number',message:'',trigger:'blur'}
        ],
      },

      //
      actionType:'',

      checks:[],

    }
  },

  methods:{
    checkRequest() {
      return checkRequest
    },
    //
    getcheckList(){

      this.$http.get('/api/check/list',{params:this.queryCondition}).then(

          res => {

            this.checkPage=res.data.data.checkPage

          }
      )
    },

    //
    handleSizeChange(val){

      this.queryCondition.pageSize = val

      this.getcheckList();

    },

    //
    handleCurrentChange(val){

      this.queryCondition.currentPage = val

      this.getcheckList();

    },

    //
    add(){

      this.formData={}

      this.actionType=''

      this.openDialogFlag = true

    },

    addOrModify(){
      this.$refs.formRef.validate(

          valid => {

            if(valid){

              if(this.actionType==''){

                this.$http.post('/api/check/add',this.formData).then(

                    res => {

                      if(res.data.status==1){

                        this.$message.success(res.data.message)

                        this.getcheckList()

                      }else{

                        this.$message.error(res.data.message)
                      }
                    }
                )
              }else{
                this.$http.put('/api/check/modify',this.formData).then(

                    res => {

                      if(res.data.status==1){

                        this.$message.success(res.data.message)

                        this.getcheckList();

                      }else{

                        this.$message.error(res.data.message)
                      }
                    }
                )
              }

              this.openDialogFlag=false

            }else{

              this.$message.error("")
            }
          }
      )
    },

    //
    modify(checkId) {

      this.$http.get('/api/check/get/' + checkId).then(

          res => {
            if(res.data.status==1){

              this.formData =res.data.data.check

              this.actionType='';

              this.openDialogFlag=true;

            }else{

              this.$message.error(res.data.message);

            }
          }
      )
    },

    //
    async remove(checkId,checkName){

      let removeEnableFlag = false;

      await this.$http.get('/api/check/removeEnable/' + checkId).then(

          res => {

            removeEnableFlag = (res.data.status==1)

          })

      if(removeEnableFlag){

        const result = await this.$confirm(

            ''+ checkName + '',
            '',

            {
              confirmButtonText:'',
              cancelButtonText:'',
              type:'warning'

            })

        if(result=='confirm'){

          this.$http.delete('/api/check/remove/'+checkId).then(

              res => {

                if(res.data.status==1){

                  this.$message.success(res.data.message)

                  this.getcheckList()

                }else{

                  this.$message.error(res.data.message)

                }
              }
          )
        }
      }else{

        this.$message.error('')

      }
    },

    //
    handleSelectionChange(val){

      this.checks=val
    },

    //
    async deleteBatch(){

      let checkIds=''
      let checkNames=''

      this.checks.forEach(check =>{
        checkIds += check.checkId + ','
        checkNames += check.checkName + ','
      })

      checkIds=checkIds.substring(0,checkIds.length - 1)
      checkNames=checkNames.substring(0,checkNames.length - 1)

      const result =await this.$confirm(
          ''+ checkNames + '',
          '',
          {
            confirmButtonText:'',
            cancelButtonText:'',
            type:'warning'
          })

      if(result=='confirm'){

        this.$http.delete('/api/check/deleteBatch?checkIds='+checkIds).then(

            res => {

              if(res.data.status==1){

                this.$message.success(res.data.message)

                this.getcheckList()

              }else{

                this.$message.error(res.data.message)

              }
            }
        )
      }
    }
  },


  mounted(){
    this.getcheckList();
  }
}
</script>

<style scoped>
.add-button{
  position: absolute;
  right: 40px;
}
.delete-button{
  position: absolute;
  right: 200px;
}
</style>
