<template>
  <div>

    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/welcome'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-input v-model="queryCondition.doctorName" placeholder="" style="width: 200px"/>
      <el-select style="width: 160px;margin-right: 25px" v-model="queryCondition.doctorType" placeholder="" @change="changeDoctorType">
        <el-option label="" value=""></el-option>
        <el-option v-for="doctorType in doctorTypes" :value="doctorType.typeId" :label="doctorType.typeName"/>
      </el-select>
      <el-select style="width: 160px;margin-right: 25px" v-model="queryCondition.deptId" placeholder="" v-if="queryCondition.doctorType===1 || queryCondition.doctorType === 2">
        <el-option label="" value=""></el-option>
        <el-option v-for="dept in deptList" :value="dept.deptId" :label="dept.deptName"/>
      </el-select>
      <el-select style="width: 160px;margin-right: 25px" v-model="queryCondition.doctorType" placeholder="" v-if="queryCondition.doctorType===1">
        <el-option label="" value=""></el-option>
        <el-option v-for="level in levelList" :value="level.levelId" :label="level.levelName"/>
      </el-select>
      <el-button type="primary" style="margin-right: 30px" @click="getDoctorList"></el-button>
      <el-button type="danger" @click="deleteBatch" ></el-button>
      <el-button type="primary" @click="add"></el-button>
      <!--  -->
      <el-table :data="doctorVOPage.records" style="width: 80%" stripe border @selection-change="handleSelectionChange">

        <el-table-column type="selection"/>
        <el-table-column label="">
          <template slot-scope="scope" v-if="scope.row.photo != null">
            <el-avatar :src="'/api/image/photo/'+scope.row.photo" :size="50"></el-avatar>
          </template>
        </el-table-column>
        <el-table-column prop="doctorName" label="">
        </el-table-column>
        <el-table-column prop="doctorType" label="">
        </el-table-column>
        <el-table-column prop="deptName" label="">
        </el-table-column>
        <el-table-column prop="levelName" label="">
        </el-table-column>
        <el-table-column prop="scheduleName" label="">
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="modify(scope.row.doctorId)" ></el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="remove(scope.row.doctorId,scope.row.doctorName)"></el-button>
          </template>
        </el-table-column>
      </el-table>


      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryCondition.currentPage"
          :page-sizes="[1, 4, 5, 10]"
          :page-size="queryCondition.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="doctorVOPage.total">
      </el-pagination>

    </el-card>
    <el-dialog :title="actionType" :visible.sync="OpenDialog" width="30%" center>
      <el-form :model="formData" :rules="formRules" ref="formRef">
        <el-form-item label="" label-width="100px" prop="doctorName">
          <el-input v-model="formData.doctorName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model.number="formData.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="doctorType">
          <el-select v-model="formData.doctorType" placeholder="" @change="changeDoctorType">
            <el-option v-for="doctorType in doctorTypes" :value="doctorType.typeId" :label="doctorType.typeName"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="deptId" v-if="formData.doctorType==1">
          <el-select v-model="formData.deptId" placeholder="">
            <el-option v-for="dept in deptList" :value="dept.deptId" :label="dept.deptName"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="levelId" v-if="formData.doctorType==1">
          <el-select v-model="formData.levelId" placeholder="">
            <el-option v-for="level in levelList" :value="level.levelId" :label="level.levelName"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="scheduleId" v-if="formData.doctorType==1">
          <el-select v-model="formData.scheduleId" placeholder="">
            <el-option v-for="schedule in scheduleList" :value="schedule.scheduleId" :label="schedule.scheduleName"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-upload action="/api/doctor/upload" :on-success="uploadSuccess" :before-upload="beforeUpload" list-type="picture" :limit="1">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" style="color: red;font-size: 12px">100KB</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="OpenDialog = false"> </el-button>
        <el-button type="primary" @click="addOrModify"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>


export default{

  //

  data() {

    return {

      doctorVOPage: {},

      queryCondition:{
        doctorName:'',
        doctorType:'',
        levelId:'',
        deptId:'',
        currentPage:1,
        pageSize:10,
      },
      drugTypes:['','2', '1'],
      OpenDialog:false,
      actionType:'',
      formData:{
        doctorName:'',
        doctorId: 0,
        password:'',
        levelId:0,
        deptId:'',
        scheduleId:'',
        doctorType: ''
      },
      formRules:{
        doctorName:[
          { required:true, message:'',trigger:'blur'},
          { max:10, message: '',trigger:'blur'}
        ],
        password:[
          { required:true, message:'',trigger:'blur'},
          { max:10, message: '',trigger:'blur'}
        ],

        doctorType:[
          { required:true, message:'',trigger:'blur'},
        ],
        levelId:[
          { required:true, message:'',trigger:'blur'},
        ],
        deptId:[
          { required:true, message:'',trigger:'blur'},
        ],
        scheduleId:[
          { required:true, message:'',trigger:'blur'},
        ],
      },
      doctors:[],
      doctorTypes:[
        {typeId:1,typeName: ''},
        {typeId:2,typeName: ''},
        {typeId:3,typeName: ''},
        {typeId:4,typeName: ''}
      ],
      doctorDeptList:[],
      checkDeptList:[],
      deptList:[],
      levelList:[],
      scheduleList:[]
    }
  },
  methods: {

    beforeUpload(file){
      if(file.type.substring(0,5) != 'image'){
        this.$message.error("")
        return false
      }
      if(file.size > 1024 * 100){
        this.$message.error("100KB")
        return false
      }
      return true
    },
    uploadSuccess(res,file,fileList){
      if(res.status == 1){
        console.log(res)
        this.formData.photo = res.data.serverFilename
        this.$message.success(res.message)
      }
    },
    changeDoctorType(doctorType){
      if(doctorType == 1){
        this.deptList = this.doctorDeptList
        console.log(this.deptList)
      }else if(doctorType == 2){
        this.deptList = this.checkDeptList
      }
      this.queryCondition.deptId = ''
      this.queryCondition.levelId = ''
      this.queryCondition.scheduleId = ''
    },
    getDoctorList(){
      this.$http.get('/api/doctor/list', {params:this.queryCondition}).then(
          res => {

            this.doctorVOPage = res.data.data.doctorVOPage

            this.doctorVOPage.records.forEach(doctor =>{
              if(doctor.doctorType === 1){
                doctor.doctorType= ''
              }else if(doctor.doctorType === 2){
                doctor.doctorType= ''
              }else if(doctor.doctorType === 3){
                doctor.doctorType= ''
              }else {
                doctor.doctorType= ''
              }
            })
          }
      )
    },
    handleSizeChange(val) {
      this.queryCondition.pageSize = val;
      this.getDoctorList();
    },
    handleCurrentChange(val) {
      this.queryCondition.currentPage = val;
      this.getDoctorList();
    },
    handleSelectionChange(val){
      this.doctors = val
    },

    add(){
      this.formData={}

      this.actionType=''

      this.OpenDialog=true
    },
    modify(doctorId){
      this.$http.get('/api/doctor/get/'+doctorId).then(
          res => {
            if(res.data.status == 1){
              this.formData = res.data.data.doctor;

              this.actionType='';

              this.OpenDialog=true;

            }else{
              this.$message.error(res.data.message)
            }
          }
      )
    },
    addOrModify(){
      this.$refs.formRef.validate(
          valid =>{
            if(valid){
              if(this.actionType===''){
                this.$http.post('/api/doctor/add', this.formData).then(
                    res => {
                      if(res.data.status == 1){
                        this.$message.success(res.data.message)

                        this.getDoctorList()
                      }else{
                        this.$message.error(res.data.message)
                      }
                    }
                )
              }else{
                this.$http.put('/api/doctor/modify', this.formData).then(
                    res => {
                      if(res.data.status === 1){
                        this.$message.success(res.data.message)

                        this.getDoctorList()
                      }else{
                        this.$message.error(res.data.message)
                      }
                    }
                )
              }
              this.OpenDialog= false
            }else{
              this.$message.error("")
            }
          }
      )
    },
    deleteBatch(){

      let doctorIds = ''
      let doctorNames = ''

      this.doctors.forEach( doctor => {
        doctorIds += doctor.doctorId + ','
        doctorNames += doctor.doctorName + ','
      })

      doctorIds = doctorIds.substring(0, doctorIds.length - 1)
      doctorNames = doctorNames.substring(0, doctorNames.length - 1)

      const result = this.$confirm(
          '' + doctorNames + '','',
          {
            confirmButtonText:'',
            cancelButtonText:'',
            type:'warning'
          }
      ).then(() => {

        this.$http.delete('/api/doctor/deleteBatch?doctorIds='+doctorIds).then(
            res => {
              if(res.data.status == 1){
                this.$message.success(res.data.message)

                this.getDoctorList()
              }else{
                this.$message.error(res.data.message)
              }
            }
        )
      }).catch(() => {})
    },

    remove(doctorId, doctorName){

      this.$confirm(
          ''+doctorName+'','',
          {
            confirmButtonText:'',
            cancelButtonText:'',
            type:'warning'
          }
      ).then(() => {
        this.$http.delete('/api/doctor/remove/'+doctorId).then(
            res => {
              if(res.data.status == 1){
                this.$message.success(res.data.message)

                this.getDoctorList()
              }else{
                this.$message.error(res.data.message)
              }
            }
        )
      }).catch(() => {})
    },
  },

  mounted(){
    this.$http.get('/api/dept/listAll/1').then( res => {
      this.doctorDeptList = res.data.data.deptList
    })
    this.$http.get('/api/dept/listAll/2').then(
        res => {
      this.checkDeptList = res.data.data.deptList
    })
    this.$http.get('/api/level/listAll').then( res => {

      this.levelList = res.data.data.levelList
    })
    this.$http.get('/api/schedule/listAll').then( res => {
      this.scheduleList = res.data.data.scheduleList
    })
    console.log(this.doctorDeptList)
    this.getDoctorList();
  }
}

</script>

<style scoped>
.tag{
  margin-right: 20px;
  margin-left: 10px;
}
</style>
